import { React, useEffect, useRef, useState } from 'react';
import Slider from 'react-input-slider';
import './Control.scss';

import {
    FaRandom,
    FaFastForward,
    FaFastBackward,
    FaPlay,
    FaPause,
    FaVolumeUp,
    FaListUl

} from 'react-icons/fa';
import { RiRepeatFill } from 'react-icons/ri';
import MyList from './MyList';


function Control(props) {
    const [range, setRange] = useState({ x: 0 });
    const [isChange, setIsChange] = useState(true);
    const [timeEnd, setTimeEnd] = useState("00:00");
    const audio = useRef(null);
    const nextBtn = useRef(null);
    const progress = useRef(null);
    const timeStartElement = useRef(null);
    const timeEndElement = useRef(null);
    const { playList,
        currentSongIndex,
        setCurrentSongIndex,
        isPlaying,
        setIsPlaying,
        myList,
        openMyList,
        setOpenMyList
    } = props;
    const currentSong = playList[currentSongIndex]

    function handleSetOpenMyList() {
        setOpenMyList(!openMyList);
    }

    useEffect(() => {
        if (isPlaying) {
            audio.current.play();
        } else {
            audio.current.pause();
        }
    });

    function handleonLoadedMetadata() {
        let newTimeEnd = getSongTime(audio.current.duration * 1000);
        setTimeEnd(newTimeEnd)
    }

    function handleEndSong() {
        setTimeout(() => {
            nextBtn.current.click();
        }, 100);
    }

    function handleClickPlay() {
        timeEndElement.current.textContent = getSongTime(audio.current.duration * 1000);
    }

    function handleTimeUpdate(e) {
        if (isChange) {
            let currentTime = (e.currentTarget.currentTime / e.currentTarget.duration * 100).toFixed(1);
            timeStartElement.current.textContent = getSongTime((e.currentTarget.currentTime).toFixed() * 1000);
            setRange(({ x: currentTime }));
        }
    }

    function handleEndDrag() {
        let numberChange = Number(progress.current.dataset.value);
        const timePresent = audio.current.duration * (numberChange / 100);
        audio.current.currentTime = timePresent;
        setIsChange(true);
    }

    function handleDrag() {
        setIsChange(false);
    }

    function handleChangeTime(x) {
        const timePresent = Math.floor(audio.current.duration * (x / 100)) * 1000;
        timeStartElement.current.textContent = getSongTime(timePresent);
    }

    const SkipSong = (forwards = true) => {
        if (forwards) {
            setCurrentSongIndex(() => {
                let temp = currentSongIndex;
                temp++;

                if (temp > playList.length - 1) {
                    temp = 0;
                }
                return temp
            });
        } else {
            setCurrentSongIndex(() => {
                let temp = currentSongIndex;
                temp--;

                if (temp < 0) {
                    temp = playList.length - 1;
                }

                return temp;
            });
        }
        isPlaying ? audio.current.play() : setIsPlaying(!isPlaying);
    }

    function getSongTime(millisec) {
        var seconds = (millisec / 1000).toFixed(0);
        var minutes = Math.floor(seconds / 60);
        var hours = "";
        if (minutes > 59) {
            hours = Math.floor(minutes / 60);
            hours = (hours >= 10) ? hours : "0" + hours;
            minutes = minutes - (hours * 60);
            minutes = (minutes >= 10) ? minutes : "0" + minutes;
        }

        seconds = Math.floor(seconds % 60);
        seconds = (seconds >= 10) ? seconds : "0" + seconds;
        if (hours !== "") {
            return hours + ":" + minutes + ":" + seconds;
        }
        return "0" + minutes + ":" + seconds;
    }


    return (
        <div className="control absolute-md">
            <div className="control-container flex">
                <div
                    className="item btn-prev"
                    onClick={() => SkipSong(false)}
                >
                    <button className="control-button">
                        <FaFastBackward />
                    </button>
                </div>
                <div
                    className="item btn-play"
                    onClick={() => {
                        setIsPlaying(!isPlaying)
                        handleClickPlay()
                    }}
                >
                    <button className="control-button">
                        {isPlaying ? <FaPause /> : <FaPlay />}
                    </button>
                </div>
                <div
                    className="item btn-next"
                    onClick={() => SkipSong()}
                >
                    <button className="control-button" ref={nextBtn}>
                        <FaFastForward />
                    </button>
                </div>
                <div className="item listBtn">
                    <button
                        className="control-button btnList"
                        onClick={() => handleSetOpenMyList()}
                    >
                        <FaListUl />
                    </button>
                    <div className={`playerList ${openMyList && "active"}`}>
                        <ul className="list">
                            {myList.map(item => (
                                <MyList value={item} key={item.id} />
                            ))}
                        </ul>
                    </div>

                </div>
                <form className="item btn-range" data-value={range.x} ref={progress}>
                    <span className="name-singer">
                        {`${currentSong.name} - ${currentSong.singer}`}
                    </span>
                    <Slider
                        styles={{
                            track: {
                                width: '100%',
                                marginBottom: "20px",
                                height: "5px",
                                cursor: "pointer"
                            },
                            active: {
                                backgroundColor: '#657c93',
                            },
                            thumb: {
                                width: 10,
                                height: 10
                            }
                        }}
                        axis="x"
                        x={range.x}
                        onDragStart={handleDrag}
                        onChange={({ x }) => {
                            setRange(({ x }))
                            handleChangeTime(x)
                        }}
                        onDragEnd={() => handleEndDrag()}
                    />
                </form>
                <div className="item time" ref={timeStartElement}>00:00</div>
                <div className="item time" ref={timeEndElement}>{timeEnd}</div>
                <div className="item btn-mute">
                    <button className="control-button">
                        <FaVolumeUp />
                    </button>
                </div>
                <div className="item btn-mute">
                    <button className="control-button">
                        <FaRandom />
                    </button>
                </div>
                <div className="item btn-mute">
                    <button className="control-button">
                        <RiRepeatFill />
                    </button>
                </div>
            </div>
            <audio
                ref={audio}
                src={process.env.PUBLIC_URL + currentSong.path}
                onLoadedMetadata={() => handleonLoadedMetadata()}
                onEnded={() => handleEndSong()}
                onTimeUpdate={(e) => handleTimeUpdate(e)}
            ></audio>
        </div >
    );
}

export default Control;