import React from 'react';
import {
    FaPlay,

} from 'react-icons/fa';
import { RiCloseLine } from 'react-icons/ri';

function MyList(props) {
    const item = props.value;
    return (
        <li>
            <div className="playerSong">
                <span className="btnMyPlay">
                    <FaPlay />
                </span>
                <span className="song-name"><strong>{item.name}</strong> by {item.singer}</span>
                <span className="remove"><RiCloseLine /></span>
            </div>
        </li>
    );
}

export default MyList;